@import "../utils/colors.scss";

#landing {
    position:relative;
    max-width:100dvw;
    height:100dvh;

    h1 {
        position:absolute;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);

        background-color:rgba(255, 255, 255, 0.7);
        padding:20px;
        border-radius:30px;
        border:$heavyGreen solid 2px;

        font-size:2.5em;
        text-align:center;
    }

    span {
        color:$green;
    }

    img {
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
        transition: ease-in-out;
        object-fit: cover;
    }
}

.noParallax {
    display:none
}

@media screen and (max-width:1024px) {
    #landing .noParallax {
        display:block;
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
        transition: ease-in-out;
        object-fit: cover;
    }

    .parallax {
        display:none;
    }

    
}

@media screen and (max-width:500px){
    #landing .noParallax h1 {
        font-size: 1.7em;
    }
}